import React, { Component } from 'react';
import {Icon,Menu, Segment, Sidebar,Accordion } from 'semantic-ui-react';
import {Link,Switch,Route} from 'react-router-dom';
import UserLanding from 'components/Landing/UserLanding/UserLanding';
import DeviceUI from 'components/DeviceUI/SingleDevice/DeviceUI';
import MultipleDeviceUI from 'components/DeviceUI/BulkDevices/BulkDevices';
import './UserDashboard.css';


class UserDashboard extends Component{
    state = { activeIndex: 1 }

    handleClick = (e, titleProps) => {
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index
    
        this.setState({ activeIndex: newIndex })
    }
    render() {
      
        return (
            <div className='user-dashboard-main'>
                <Sidebar.Pushable as={Segment} className='user-sidebar-pushable'>
                <Sidebar
                    as={Menu}
                    animation='push'
                    icon='labeled'
                    inverted
                    vertical
                    visible={true}
                    width='thin'
                >
                    <Menu.Item as={Link} to="/dashboard/landingpage">
                        <Icon name='user' />
                       View Devices
                    </Menu.Item>
                    <Menu.Item as={Accordion} >
                        <Accordion.Title style={{color:'white'}} active={this.state.activeIndex === 0} index={0} onClick={this.handleClick}>
                             <Icon name='dropdown' />
                                Add  Devices
                        </Accordion.Title>

                        <Accordion.Content active={this.state.activeIndex === 0}>
                            <Menu.Item as={Link} to="/dashboard/newdevice">
                                <Icon name='plus circle' />
                                Add Single Device
                            </Menu.Item>
                            <Menu.Item as={Link} to="/dashboard/bulkdevices">
                                <Icon name='plus circle' />
                                Add Multiple Devices
                            </Menu.Item>

                        </Accordion.Content>
                    </Menu.Item>
                    <Menu.Item as={Link} to="/" onClick={this.props.logoutHandler}>
                        <Icon name='external' />
                        Logout
                    </Menu.Item>
                </Sidebar>

                <Sidebar.Pusher >
                    <Segment basic>
                        <Switch>
                            <Route exact path="/dashboard/landingpage" component={UserLanding} />
                            <Route exact path="/dashboard/newdevice" component={DeviceUI} />
                            <Route exact path="/dashboard/bulkdevices" component={MultipleDeviceUI} />
                        </Switch>
                    </Segment>
                </Sidebar.Pusher>
                </Sidebar.Pushable>
            </div>
        )
    
    }
}

export default UserDashboard;